@import url(https://fonts.googleapis.com/css2?family=Cabin:wght@500&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Cabin', sans-serif;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  display: flex;
  justify-content: center;
}

.WorldClockApp {
    display: flex;
    flex-direction: column;
}

.WorldClockForm {
    display: flex;
    align-items: flex-end;
    padding-top: 30px;
}

.inputBlock:nth-child(n+2) {
    margin-left: 30px;
}

.inputBlock {
    display: flex;
    flex-direction: column;
}

.formButton {
    margin-left: 30px;
    padding: 10px;
}

.inputForm {
    padding: 10px;
    margin-top: 20px;
}

.wrapperClock {
    display: flex;
}

.deleteClockButton {
    border: 1px solid black;
    border-radius: 50%;
    font-size: 0.5rem;
    padding: 5px 7px;
    cursor: pointer;
}

.ClockList {
    margin-top: 30px;
}

.ClockComponent {
    list-style: none;
    display: inline-block;
}

.ClockComponent:nth-child(n + 2) {
    margin-left: 30px;
}

.clockComponentBody {
    display: flex;
    align-items: flex-start;
    margin-top: 20px;
}

.clockText {
    text-align: center;
    margin-right: 20px;
}

